import React, { useState, useEffect } from "react";
import "./IngredientsDesktop.scss";
import * as ScrollMagic from "scrollmagic";
import gsap from "gsap";
import CheeseIngredients2 from "./images/sery-pomidor.png";
import CheeseIngredients3 from "./images/papryka-bazylia.png";

const IngredientsDesktop = () => {
  // eslint-disable-next-line
  const [scrollMagic, setScrollMagic] = useState({
    controller: new ScrollMagic.Controller(),
    timelineOne: gsap.timeline(),
  });
  const { controller, timelineOne } = scrollMagic;
  useEffect(() => {
    // ingredients animation
    timelineOne.staggerFromTo(
      [".cheeseIngredients2", ".cheeseIngredients3"],
      0.5,
      { y: "50px", opacity: 0 },
      { y: "0", opacity: 1 },
      0.25
    );

    new ScrollMagic.Scene({
      triggerElement: ".ingredients_desktop2",
      triggerHook: 0.7,
    })
      .setTween(timelineOne)
      .addTo(controller);
    // eslint-disable-next-line
  }, []);
  return (
    <div className="ingredients_desktop2">
      <img
        src={CheeseIngredients2}
        alt="Sery bazylia"
        className="cheeseIngredients2"
      />
      <img
        src={CheeseIngredients3}
        alt="Pomidor bazylia"
        className="cheeseIngredients3"
      />
    </div>
  );
};

export default IngredientsDesktop;
